.flex {
  display: flex;
  align-items: center;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.absolute-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.full-screen {
  background: rgba(55, 55, 55, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.button-item {
  font-size: 12px;
  border-radius: 2px;
  cursor: pointer;
  min-width: 70px;
  padding: 0 5px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: inline-flex;
}
.button-item.primary-btn {
  background: #5582F3;
  color: #FFF;
}
.button-item.primary-btn:hover {
  opacity: 0.7;
}
.button-item.primary-btn:active {
  background: #5475D3;
  opacity: 1;
}
.button-item.secondary-btn {
  border: 1px solid #BBB;
  color: #666;
  opacity: 1;
}
.button-item.secondary-btn:active,
.button-item.secondary-btn:hover {
  border-color: #6481E4;
  color: #698CED;
}
.cell-ctn {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.input-ctn {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
.input {
  width: calc(100% - 20px);
  height: 85%;
  border: none;
  outline: none;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.input.active-input:not(:disabled):focus,
.input.active-input:not(:disabled):active,
.input.active-input-box:not(:disabled) {
  border: 1px solid #999;
  padding-left: 8px;
}
.suffix-icon {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%) scale(0.7);
}
.type-ctn {
  position: absolute;
  top: 100%;
  left: 0;
  width: 95%;
  background: #fff;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.15);
  font-size: 12px;
  color: #333;
  z-index: 9;
  max-height: 100px;
  overflow-y: auto;
}
.type-item {
  line-height: 25px;
  padding-left: 8px;
  cursor: pointer;
}
.type-item:hover {
  background: rgba(85, 130, 243, 0.1);
  color: #666;
}
