.switch-box {
  width: 25px;
  height: 15px;
  position: relative;
  overflow: hidden;
  border-radius: 50px;
  background: #c4c1c1;
  transition: all 0.2s ease;
}
.switch-box.switch-box-checked {
  background: #18BC71;
}
.switch-box.switch-box-checked:before {
  left: 11px;
}
.switch-box:before {
  position: absolute;
  content: "";
  width: 12px;
  height: 13px;
  background: #fff;
  border-radius: 100%;
  top: 1px;
  left: 1px;
  transition: left 0.2s ease;
}
.check-input {
  width: 100%;
  height: 100%;
  opacity: 0;
}
