.flex {
  display: flex;
  align-items: center;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.absolute-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.full-screen {
  background: rgba(55, 55, 55, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.button-item {
  font-size: 12px;
  border-radius: 2px;
  cursor: pointer;
  min-width: 70px;
  padding: 0 5px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: inline-flex;
}
.button-item.primary-btn {
  background: #5582F3;
  color: #FFF;
}
.button-item.primary-btn:hover {
  opacity: 0.7;
}
.button-item.primary-btn:active {
  background: #5475D3;
  opacity: 1;
}
.button-item.secondary-btn {
  border: 1px solid #BBB;
  color: #666;
  opacity: 1;
}
.button-item.secondary-btn:active,
.button-item.secondary-btn:hover {
  border-color: #6481E4;
  color: #698CED;
}
.alert-ctn {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  padding: 0 22px;
  font-size: 14px;
  justify-content: space-between;
  display: flex;
  align-items: center;
}
.alert-ctn.alert-warn-ctn {
  background: #FFF3C7;
  color: #ED812B;
}
.alert-ctn.alert-success-ctn {
  background: #E6F5F1;
  color: #26B175;
}
.alert-ctn.alert-error-ctn {
  color: #FD6C6A;
  background: #FFE6E4;
}
