.flex {
  display: flex;
  align-items: center;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.absolute-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.full-screen {
  background: rgba(55, 55, 55, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.button-item {
  font-size: 12px;
  border-radius: 2px;
  cursor: pointer;
  min-width: 70px;
  padding: 0 5px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: inline-flex;
}
.button-item.primary-btn {
  background: #5582F3;
  color: #FFF;
}
.button-item.primary-btn:hover {
  opacity: 0.7;
}
.button-item.primary-btn:active {
  background: #5475D3;
  opacity: 1;
}
.button-item.secondary-btn {
  border: 1px solid #BBB;
  color: #666;
  opacity: 1;
}
.button-item.secondary-btn:active,
.button-item.secondary-btn:hover {
  border-color: #6481E4;
  color: #698CED;
}
.wrapper {
  width: 99%;
  padding-top: 30px;
  padding-left: 15px;
}
.form {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.form-item {
  font-size: 12px;
  color: #666;
}
.form-item:nth-child(1) {
  width: 300px;
}
.form-item:nth-child(2) {
  flex: 1;
}
.form-item-input {
  outline: none;
  border: none;
  border-bottom: 1px solid #ccc;
  width: 80%;
  height: 24px;
  margin-top: 5px;
}
.btn-ctn {
  display: flex;
  align-items: center;
  color: #5E80EB;
  font-size: 14px;
  margin-bottom: 20px;
}
.btn-ctn .btn-item {
  cursor: pointer;
  line-height: 14px;
  padding: 0 10px;
}
.btn-ctn .btn-item:first-child {
  margin-left: auto;
}
.btn-ctn .btn-item:last-child {
  padding-right: 0;
}
.btn-ctn .btn-item + .btn-item {
  border-left: 1px solid #C0C6CD;
}
.title-item {
  line-height: 30px;
  position: relative;
  background: #e6e4e4;
  white-space: nowrap;
  padding-left: 10px;
}
.title-item:not(:last-child)::after {
  position: absolute;
  content: "";
  height: 50%;
  border-left: 1px solid #999;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}
.title-item:nth-child(1),
.title-item:nth-child(3) {
  padding-left: 0;
  text-align: center;
}
.table-wrapper {
  width: 100%;
  min-height: 100px;
  overflow-x: auto;
}
.table-ctn {
  width: 99%;
  table-layout: fixed;
  border: 1px solid #999;
  text-align: left;
}
.row-wrapper {
  position: relative;
  height: 40px;
}
.cell-item {
  position: relative;
  border-top: 1px solid #999;
  padding-left: 10px;
}
.cell-item:nth-child(1),
.cell-item:nth-child(3) {
  padding-left: 0;
  text-align: center;
}
.cell-inner-ctn {
  height: 40px;
  display: flex;
  align-items: center;
}
.button-ctn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px 0 5px;
}
.button-item + .button-item {
  margin-left: 35px;
}
.select-box {
  width: 16px;
  height: 16px;
  border-radius: 100%;
  border: 1px solid #999;
  position: relative;
  margin-left: 20px;
}
.select-box.checked::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background: #5582F3;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease;
}
.prefix-label {
  white-space: nowrap;
}
